* {
  box-sizing: border-box;
}

#footer {
  width: 100%;
  display: flex;
  margin: auto;
  align-items: center;
  justify-content: center;
  user-select: none;
}

.box {
  height: 100px;
  width: 125px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 2px solid cadetblue;
  background-color: #78b2b4;
  color: black;
  transition: all 0.5s;
}

.player-name,
#tilesHeading {
  font-size: 1rem;
  font-weight: 500;
}

.score,
#filledTilesCount {
  font-weight: 500;
  font-size: 1.5rem;
}
